@use '../../styles/variables' as vars;

.app-header {
    background-color: vars.$bg-color;
    border-bottom: 1px solid vars.$border-color;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .project-info {
        h1 {
            margin: 0;
            font-size: 24px;
            font-weight: bold;
        }
    }
}
